@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;1,700&display=swap);
body {
  margin: 0;
  font-family: 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}


html, body {
  width: inherit;
  height: inherit;
  font-family: Lato, Oxygen, sans-serif, source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  background-color: #f0efeb;
}

.container {
  display: flex;
  flex-direction: row;
}

.nav-container {
  position: fixed;
  padding: 250px 0px 20px 200px;
}

.main-container {
  position: relative;
  margin-left: 20vw;
  padding: 200px 0px 20px 100px;

  width: 60vw;
}


.nav {
    display: flex;
    flex-direction: column;
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    font-size: 20px;
}

a {
    color: black;
    text-decoration: none;
}

a:hover {
    color: #ff836d !important;
    text-decoration: underline !important;
}

.nav a {
    padding-bottom: 20px;
}

.link {
    margin-bottom: 15px;
}

.link:last-child {
    margin-bottom: 0px;
}
a {
    color: black;
    text-decoration: none;
}

a:hover {
    color: black; 
    text-decoration: underline;
    -webkit-text-decoration-color: #ff836d;
            text-decoration-color: #ff836d;
}

.project {
    display: flex;
    flex-direction: column;
    color: #ff836d;
    justify-content: flex-start;

    height: -webkit-fit-content;

    height: -moz-fit-content;

    height: fit-content;
    width: 25vw;
}

.project:hover {
    text-decoration: underline;
}

.index {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    font-size: 28px;
    margin-bottom: 10px;
}

.image {
    height: 250px;
    margin-bottom: 20px;
}

img {
    height: auto;
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
}

.category {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    font-size: 14px;
    margin-bottom: 10px;
}

.title {
    font-size: 24px;
    color: black;
    margin-bottom: 10px;
}

.desc {
    font-size: 18px;
    color: grey;
    line-height: 1.5;
}
.main {
    display: flex;
    flex-direction: column;
    margin-top: -100px;
}

.header {
    font-size: 40px;
    width: 50vw;
    padding-bottom: 100px;
    line-height: 1.5;
    font-style: italic;
    font-weight: 700;
}

.projects {
    display: grid;
    grid-template-columns: auto auto;
    
    width: -webkit-fit-content;
    
    width: -moz-fit-content;
    
    width: fit-content;
    background-color: #fae1dd;
    padding: 50px;
    grid-column-gap: 30px;
}

.container:nth-child(even) {
    margin-top: 50px;
}
.page {
    display: flex;
    flex-direction: column;
    margin-top: -100px !important;
}

.header {
    font-size: 40px;
    font-style: italic;
    font-weight: 700;
    width: 50vw;
    padding: 0px;
    padding-bottom: 50px;
} 

p {
    font-size: 20px;
    line-height: 2;
    padding-bottom: 20px;
}

.navi {
    float:right;
}

.navi p {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    font-size: 18px;
    font-weight: 700;
    line-height: 0;
    padding-bottom: 15px;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    padding-bottom: 15px;
}

ul a {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    font-size: 18px;
}

ul a:hover {
    color: #ff836d;
    text-decoration: underline;
}

#note {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    font-size: 15px;
    line-height: 2;
    padding-top: 20px;
}
.page {
    display: flex;
    flex-direction: column;
    margin-top: -150px;
}

.header-image {
    margin-bottom: 20px;
}

h1 {
    font-size: 40px;
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    margin-top: 10px;
    margin-bottom: 20px;
}

h2 {
    font-size: 24px;
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    margin-bottom: 0px !important;
}

h3 {
    font-size: 20px;
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    margin-bottom: 0px !important;
}

p {
    margin-bottom: 10px;
    line-height: 2;
}

.bulletpoint {
    margin-left: 40px;
}

ul.list {
    list-style-type: circle;
}

.key-para {
    background-color: rgba(255, 255, 255, 0.6);
    padding: 50px;
}

.image-grid {
    display: grid;
    grid-template-columns: auto auto; 
    grid-row-gap: 20px; 
    row-gap: 20px;
    justify-items: center;
}

table {
    width: 90%;
    font-size: 17px;
}

th, td {
    padding: 10px;
    text-align: center;
}

table, th, td {
    border-collapse: collapse;
}

tr {
    background-color: rgba(255, 255, 255, 0.7);
}

th {
    background-color: rgba(163, 159, 144, 0.4);
}
